@import url(https://fonts.googleapis.com/css2?family=Amiri&family=Fira+Sans+Condensed:wght@300;400;500&display=swap);
html,
* {
  font-family: "Fira Sans Condensed", sans-serif;
  font-weight: 300;
  background-color: white;
  box-sizing: border-box;
  border: 0;
  padding: 0;
  margin: 0;
}

.App {
  position: relative;
}

.header {
  padding: 2rem;
}

/* BRANDING */
.audacitee_link {
  text-decoration: none;
  color: black;
}

.header_audacitee {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.header_title {
  font-family: "Amiri", serif;
  font-size: 3rem;
}

.header_logo {
  height: 100px;
}

/* --------------NAV---------------- */
.header_nav {
  display: flex;
  position: absolute;
  top: 2rem;
  right: 2rem;
  z-index: 20;
  /* background-color: transparent; */
}

/* GUEST */
.nav_login {
  /* background-color: transparent; */
  color: black;
  text-decoration: none;
  padding: 0.7rem;
}

.nav_login:hover {
  color: red;
  transition: color 0.4s ease-in-out;
}

/* USER */
.nav_user_dropdown {
  /* background-color: transparent; */
  display: flex;
  cursor: pointer;
  padding: 0.7rem;
}

.nav_user_dropdown:hover {
  color: red;
  transition: color 0.4s ease-in-out;
}

.user_box {
  /* background-color: transparent; */
  display: flex;
}

.user_email {
  /* background-color: transparent; */
  margin: 0 0.5rem;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.down {
  transform: rotate(0deg);
  transition: transform 0.3s ease-in-out;
}

.up {
  transform: rotate(180deg);
  transition: transform 0.3s ease-in-out;
}

.menu_box {
  border-top: 1px solid black;
  border-right: 1px solid black;
  border-left: 1px solid black;
  display: flex;
  flex-direction: column;
}

.menuTriangle {
  width: 0;
  height: 0;
  border: 8px solid transparent;
  border-top: 0;
  border-bottom: 16px solid lightgrey;
  margin-right: 0.5rem;
}

.menu_line {
  padding: 0.7rem;
}

.menu_line:hover {
  background-color: lightgrey;
  transition: background-color 0.4s ease-in-out;
}

.menu_icon {
  background-color: transparent;
  margin-right: 0.5rem;
}

.menu_link {
  background-color: transparent;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  color: black;
  text-decoration: none;
}

.logout_box {
  border: 1px solid black;
}

/* CART */
.nav_cart_box {
  padding: 0.7rem;
}

.nav_cart {
  /* background-color: transparent; */
  color: black;
  text-decoration: none;
}

.nav_cart:hover {
  color: red;
  transition: color 0.4s ease-in-out;
}

.nav_cart i {
  /* background-color: transparent; */
  margin-right: 0.3rem;
}

.products {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.products_main {
  display: grid;
  grid-gap: 0;
  grid-template-columns: repeat(4, 1fr);
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.products_fade_in {
  opacity: 1;
}

.admin_products_main {
  display: grid;
  grid-gap: 0;
  grid-template-columns: repeat(4, 1fr);
}

.admin_create_button {
  display: flex;
  color: white;
  background-color: black;
  padding: 1rem;
  text-transform: uppercase;
  margin: 1rem 0;
  align-self: flex-end;
}

.admin_link {
  text-decoration: none;
}

.admin_create_button:hover {
  background-color: rgb(102, 102, 102);
  transition: background-color 0.4s ease-in-out;
}

.admin_buttons_box {
  display: flex;
  justify-content: space-between;
}

.admin_buttons_box i {
  color: black;
}

.admin_buttons_box i:hover {
  color: red;
  transition: color 0.4s ease-in-out;
}

.products_title {
  text-transform: uppercase;
}

/* .breadcrumb_link:hover {
  color: grey;
  transition: color 0.4s ease-in-out;
} */

/* MAPPED PRODUCT */
.product {
  padding: 1rem;
  margin: 1rem;
  text-transform: uppercase;
}

.product_image {
  position: relative;
  width: 200px;
  height: 200px;
  z-index: 1;
}

.product_image img {
  max-width: 100%;
  max-height: 100%;
}

.product_overlay {
  position: absolute;
  top: 0;
  left: 0;
  background: rgb(0, 0, 0, 0);
  transition: background 0.4s ease-in-out;
  z-index: -10;
  width: 200px;
  height: 200px;
  /* opacity: 0.5; */
}

.product_image:hover .product_overlay {
  background: rgb(255, 255, 255, 0.3);
  transition: background 0.4s ease-in-out;
  z-index: 10;
}

.product_link {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.product_link:hover {
  color: red;
  /* color: rgb(119, 119, 119); */
  transition: color 0.4s ease-in-out;
}

.product_name {
  font-weight: 500;
}

/* .teeImg {
  width: 650px;
  border: 1px solid grey;
} */

.login {
  display: flex;
  /* flex-direction: column;
  align-items: center; */
  justify-content: center;
  margin: 1rem;
}

.login h1 {
  font-size: 3rem;
  font-weight: 400;
  text-transform: uppercase;
  margin-bottom: 1rem;
  padding-right: 1.5rem;
  margin-right: 1.5rem;
  border-right: 2px solid black;
  width: 13vw;
  text-align: right;
}

.login_form {
  /* border: 1px solid navy; */
  display: flex;
  /* align-items: center; */
  flex-direction: column;
  width: 15vw;
}

.login_field {
  display: flex;
  flex-direction: column;
  /* border: 1px solid blue; */
}

.login_form label {
  text-transform: uppercase;
  font-weight: 400;
}

.login_form input {
  padding: 0.5rem;
  border: 1px solid black;
}

.login_form input,
button {
  margin-bottom: 1rem;
}

.login_form button {
  color: white;
  background-color: black;
  padding: 0.5rem;
  width: 100%;
  text-transform: uppercase;
}

.login_form button:hover {
  background-color: rgb(102, 102, 102);
  transition: background-color 0.4s ease-in-out;
}

.login_form a {
  color: black;
  text-decoration: none;
}

.login_form a:hover {
  color: red;
  transition: color 0.4s ease-in-out;
}

.singleProduct {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* ------BREADCRUMBS------ */
.sp_breadcrumbs {
  display: flex;
}

.sp_breadcrumbs * {
  text-transform: uppercase;
  margin: 0 0.2rem;
}

.sp_breadcrumb_link {
  color: black;
}

.sp_breadcrumb_link:hover {
  color: red;
  transition: color 0.5s ease-in-out;
}

.sp_main {
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
}

/* .sp_left,
.sp_right {
  border: 1px solid red;
} */

/* ------LEFT------ */
.sp_left {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sp_img {
  width: 500px;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}

.sp_fade_in {
  opacity: 1;
}

/* ------RIGHT------ */
.sp_right {
  margin: 3rem 0 0 2rem;
  width: 300px;
}

.sp_right h3,
.sp_right p {
  margin-bottom: 1rem;
}

.sp_name {
  text-transform: uppercase;
  font-weight: 500;
}

.sp_form {
  display: flex;
  flex-direction: column;
}

.sp_form > div {
  margin-bottom: 1rem;
}

.sp_form label {
  margin-right: 0.5rem;
}

.sizeSelect {
  border: 1px solid black;
  padding: 0.2rem;
  margin: 0.3rem;
  cursor: pointer;
}

.quantityInput {
  border: 1px solid black;
  width: 75px;
  padding: 0.5rem;
}

.sp_submit_button {
  width: 200px;
  text-transform: uppercase;
  color: white;
  background-color: black;
  padding: 0.5rem;
}

.sp_submit_button:hover {
  background-color: rgb(102, 102, 102);
  transition: background-color 0.4s ease-in-out;
}

.sp_feedback {
  color: red;
}

.adminForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid green;
}

.adminForm h1 {
  margin: 2rem 0;
}

.adminFormMain {
  display: flex;
  flex-direction: column;
  width: 30vw;
  margin-bottom: 8rem;
}

.adminFormMain label {
  text-transform: uppercase;
  font-weight: 400;
}

.adminFormMain input {
  border: 1px solid black;
  padding: 0.5rem;
  margin-bottom: 1rem;
}

.adminFormButton {
  text-transform: uppercase;
  color: white;
  padding: 0.5rem;
  background-color: black;
}

.adminFormButton:hover {
  background-color: rgb(102, 102, 102);
  transition: background-color 0.4s ease-in-out;
}

.gcart {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* BREADCRUMBS */
.gcart_breadcrumbs {
  display: flex;
  margin-bottom: 2rem;
}

.gcart_breadcrumbs * {
  text-transform: uppercase;
  margin: 0 0.2rem;
}

.gcart_breadcrumb_link {
  color: black;
}

.gcart_breadcrumb_link:hover {
  color: red;
  transition: color 0.5s ease-in-out;
}

/* ---------------MAIN------------------ */
.gcart_main {
  display: flex;
}

.gcart_left {
  margin-right: 2rem;
}

/* -----EMPTY CART----- */
.gcart_empty {
  width: 45vw;
  /* border: 1px solid blue; */
}

.gcart_empty_img {
  /* width: 100px;
  height: 100px;
  border: 1px solid red; */
  margin-bottom: 1rem;
}

.gcart_empty_message {
  text-transform: uppercase;
  font-size: 2rem;
  margin-bottom: 1rem;
}

.gcart_empty a {
  text-decoration: none;
}

.gcart_empty_link {
  text-transform: uppercase;
  color: black;
}

.gcart_empty_link:hover {
  color: red;
  transition: color 0.4s ease-in-out;
}

/* -------CART ITEM--------- */
.gcart_item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 45vw;
  margin-bottom: 1rem;
}

.item_button {
  border: none;
  background-color: transparent;
  text-decoration: underline;
  padding: 0;
  margin: 0;
  color: grey;
}

.item_button:hover {
  color: black;
  transition: color 0.4s ease-in-out;
}

/* ITEM IMAGE */
.item_image {
  width: 200px;
}

/* ITEM INFO */
.item_info {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  height: 100%;
  margin: 0 1rem;
  flex-grow: 3;
}

.item_name_link {
  color: black;
  text-decoration: none;
}

.item_name_link:hover {
  color: red;
  transition: color 0.4s ease-in-out;
}

.item_name {
  text-transform: uppercase;
  font-weight: 500;
  padding-bottom: 0.8rem;
  margin-bottom: 0.5rem;
}

/* QUANTITY FORM */
.quantity_form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin: 0 2rem;
  flex-grow: 1;
}

.input_box {
  padding: 0.4rem;
  width: 50px;
  text-align: center;
  margin-bottom: 0.5rem;
}

/* PRICE */
.item_price {
  flex-grow: 2;
}

.item_price p {
  font-weight: 500;
}

/* -----ORDER SUMMARY--------- */
.gcart_right {
  padding: 2rem;
  background-color: rgb(231, 231, 231);
  width: 15vw;
}

.item_counter {
  background-color: transparent;
  margin-bottom: 1rem;
  /* text-transform: uppercase; */
  padding-bottom: 1rem;
  border-bottom: 1px solid black;
  font-weight: 400;
  /* text-align: right; */
}

.gcart_subtotal {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1.25rem;
  background-color: transparent;
}

.gcart_subtotal p {
  text-transform: uppercase;
  background-color: transparent;
  font-weight: 400;
}

.gcart_checkout_button {
  width: 100%;
  color: white;
  background-color: black;
  padding: 0.5rem;
}

.gcart_checkout_button:hover {
  background-color: rgb(102, 102, 102);
  transition: background-color 0.4s ease-in-out;
}

.gcheckout {
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* BREADCRUMBS */
.gcheckout_breadcrumbs {
  display: flex;
  margin-bottom: 2rem;
}

.gcheckout_breadcrumbs * {
  text-transform: uppercase;
  margin: 0 0.2rem;
}

.gcheckout_breadcrumb_link {
  color: black;
}

.gcheckout_breadcrumb_link:hover {
  color: red;
  transition: color 0.5s ease-in-out;
}

/* ---------------MAIN------------------ */
.gcheckout_main {
  display: flex;
}

/* LEFT */
.gcheckout_left {
  width: 45vw;
  margin-right: 2rem;
}

/* RIGHT */
.gcheckout_right {
  padding: 2rem;
  background-color: rgb(231, 231, 231);
  width: 15vw;
}

.summary_line {
  display: flex;
  justify-content: space-between;
  background-color: transparent;
}

.summary_line p,
.total_line p {
  background-color: transparent;
  margin-bottom: 0.5rem;
  text-transform: uppercase;
  font-weight: 400;
}

.total_line {
  display: flex;
  justify-content: space-between;
  background-color: transparent;
  margin: 2rem 0 0.5rem 0;
}

.place_order_button {
  color: white;
  background-color: black;
  padding: 0.5rem;
  width: 100%;
}

.place_order_button:hover {
  background-color: rgb(102, 102, 102);
  transition: background-color 0.4s ease-in-out;
}

.place_order_button:disabled {
  color: lightgrey;
  background-color: grey;
}

/* BOTTOM */
.bottom {
  margin-top: 2rem;
  width: 62vw;
}

/* .gcheckout_form {
  border: 1px solid magenta;
} */

.gcheckout_form h3 {
  text-transform: uppercase;
  font-weight: 500;
  margin-right: 2rem;
  padding-bottom: 1rem;
  padding-right: 1rem;
  width: 100px;
  border-right: 2px solid black;
}

.gcheckout_subform {
  display: flex;
  /* border: 1px solid blue; */
  margin-bottom: 2rem;
}

.gcheckout_form_field {
  display: flex;
  flex-direction: column;
}

.gcheckout_form_field label {
  font-size: 0.75rem;
  text-transform: uppercase;
  font-weight: 400;
  margin-bottom: 0.1rem;
}

.gcheckout_form_field input {
  font-size: 1rem;
  padding: 0.5rem;
  width: 200px;
  margin-bottom: 1rem;
  font-weight: 400;
  border: 1px solid black;
}

.gcheckout_subform_button {
  color: white;
  background-color: black;
  width: 100%;
  text-transform: uppercase;
  padding: 0.7rem;
}

.gcheckout_subform_button:hover {
  background-color: rgb(102, 102, 102);
  transition: background-color 0.4s ease-in-out;
}

/* COLLAPSED SUBFORM */
.collapsed_subform p {
  font-weight: 400;
  margin-bottom: 0.5rem;
}

.collapsed_subform_button {
  color: blue;
  background-color: transparent;
  padding: 0;
  margin: 0;
  border-top: 0;
  border-right: 0;
  border-bottom: 1px solid blue;
  border-left: 0;
}

.collapsed_subform_button:hover {
  border-bottom: 1px solid red;
  color: red;
  transition: color 0.4s ease-in-out, border-bottom 0.4s ease-in-out;
}

/* USER INFO */
.userinfo_feedback {
  margin-top: 1.5rem;
  margin-left: 1rem;
  color: red;
}

/* SHIPPING */
.shipping_feedback {
  margin-top: 27rem;
  margin-left: 2rem;
  color: red;
}

.gcheckout_shipping_options {
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  margin-bottom: 1rem;
}

.gcheckout_shipping_options p {
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: underline;
  margin-bottom: 0.6rem;
}

.shipping_field {
  display: flex;
  align-items: center;
  margin-bottom: 0.5rem;
}

.shipping_field label {
  font-weight: 400;
}

.shipping_field input {
  margin-right: 0.5rem;
}

.shipping_state_zip {
  display: flex;
}

.state {
  margin-right: 1rem;
}

.shipping_state_zip input {
  width: 92px;
}

/* PAYMENT */
.gcheckout_payment {
  display: flex;
  flex-direction: column;
}

.payment_checkbox_field input {
  margin-right: 0.5rem;
}

.payment_exp_cvv {
  display: flex;
}

.exp {
  margin-right: 1rem;
}

.payment_exp_cvv input {
  width: 92px;
}

.payment_same_address {
  margin: 1rem 0;
}

.payment_same_address p {
  margin-left: 1.5rem;
  font-weight: 400;
}

.payment_billing_address {
  margin-top: 1rem;
  margin-left: 1.5rem;
}

.payment_feedback {
  color: red;
  margin-left: 2rem;
  margin-top: 10rem;
}

.adminUsers {
  padding: 1rem;
  margin: auto;
  width: 60vw;
}

.adminUsers p {
  font-weight: 400;
}

.adminUsers h1 {
  text-transform: uppercase;
  font-weight: 400;
  text-align: center;
  margin-bottom: 2rem;
}

.userInfo {
  border-left: 2px solid black;
  padding-left: 1rem;
  padding-bottom: 1rem;
  margin-bottom: 2rem;
}

.orderInfo {
  padding: 1rem;
  margin: 1rem 0 2rem 1rem;
  border-left: 2px solid black;
  display: grid;
  grid-gap: 0;
  grid-template-columns: repeat(3, 1fr);
}

.itemInfo {
  /* border-left: 2px solid black; */
  padding: 1rem;
  margin: 1rem;
}

.teeImg {
  width: 200px;
}



.top {
  background-color: rgb(231, 231, 231);
  width: 75vw;
  margin: auto;
  padding: 4rem;
}

.thankYou {
  margin: 1rem;
  text-align: center;
  background-color: transparent;
  /* border: 1px solid blue; */
}

.thankYou h1 {
  background-color: transparent;
}

.orderConfirmation {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  background-color: transparent;
  /* border: 1px solid blue; */
}

.orderConfirmation p {
  background-color: transparent;
}

